<template>
    <div class="Category">
        <div class="categoryCenter">
            <div class="leftCategory">  
                <div v-for="tab in this.tabs" :key="tab.gender"  
                    :class="{istab:$route.query.gender === tab.gender,}"
                    class="leftCategoryList" 
                    @click="gender = tab.gender;changeGender2();changeGender1(tab.defaultmajor)">  
                    {{tab.name}}
                </div>  
            </div>
            <div class="rightCategory">               
                <div class="rightCategoryTitle">
                    <p v-if="this.gender == 'male'"></p>
                    <p v-else-if="this.gender == 'female'"></p>
                    <p v-else></p>                 
                </div>
                <div class="munu">
                    <div class="sort">
                        <div class="majorSort" v-if="gender == 'male'">
                            <ul>
                                <li v-for="list in maleMajors" 
                                :key="list.major" 
                                :class="{istab:major === list.major}"
                                @click="major = list.major;changeMajor1(list.minor);changeMajor2()">{{list.major}}</li>
                            </ul>
                        </div> 
                        <div class="majorSort" v-if="gender == 'female'">
                            <ul>
                                <li v-for="list in femaleMajors" 
                                :key="list.major" 
                                :class="{istab:major === list.major}"
                                @click="major = list.major;changeMajor1(list.minor);changeMajor2()">
                                {{list.major}}       
                                </li>
                            </ul>
                        </div>  
                        <div class="majorSort" v-if="gender == 'press'">
                            <ul>
                                <li v-for="list in pressMajors" 
                                :key="list.major" 
                                :class="{istab:major === list.major}"
                                @click="major = list.major;changeMajor1(list.minor);changeMajor2()">{{list.major}}</li>
                            </ul>
                        </div>             
                    </div>
                    <div class="moreSort" v-if="defaultMoreSort.length !== 0">
                        <span></span>
                        <ul>
                            <li v-for="list in defaultMoreSort" 
                                :key="list" 
                                :class="{istab:minor === list}"
                                @click="minor = list;changeMoreSort1(list);changeMoreSort2()">
                                {{list}}
                            </li>
                        </ul>
                    </div>
                </div>
                <div>
                    <div class="bookList">
                        <div class="book" v-for="list in categorylist.books" :key="list._id">
                            <div class="bookImg">
                                <img :src="'http://statics.zhuishushenqi.com' + list.cover">
                            </div>
                            <div class="bookinfo">
                                <p class="bookname">{{list.title}}</p>
                                <p class="author">{{list.author}}</p>
                                <p class="brief">{{list.shortIntro}}</p>
                                <p class="popular">{{list.latelyFollower}}  |  {{list.retentionRatio}}%</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
export default {     
            data () {
                return {
                    gender:'male',
                    major:'',
                    type:'hot',
                    minor:'', 
                    defaultMoreSort:['','','','',''],
                    categorylist:'',
                    tabs:[
                        {
                            gender:'male',
                            name:'',
                            defaultmajor:'',
                        },{
                            gender:'female',
                            name:'',
                            defaultmajor:'',
                        },{
                            gender:'press',
                            name:'',
                            defaultmajor:'',
                        }],
                    maleMajors:[
                        {major:'',minor:['','','','','']},
                        {major:'',minor:['',,'' ,'' ,'' ,'']},
                        {major:'',minor:['','','','']},
                        {major:'',minor:['','','','','']},
                        {major:'',minor:['','','','','','','']},
                        {major:'',minor:['','','','']},
                        {major:'',minor:['','','','']},
                        {major:'',minor:['','','','','','']},
                        {major:'',minor:['','','','','']},
                        {major:'',minor:['','','','','']},
                        {major:'',minor:[ '','','','','','','','']},
                        {major:'',minor:['','','','','']},
                        {major:'',minor:['','','','','','']},
                        {major:'',minor:[]}
                    ],
                    femaleMajors:[
                        {major:'',minor:['','','','','','']},
                        {major:'',minor:['','','','','','']},
                        {major:'',minor:[]},
                        {major:'',minor:['','','']},
                        {major:'',minor:['','','']},
                        {major:'',minor:['','','']},
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                        {major:'',minor:['','','']},
                        {major:'',minor:['','','','','','']},
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                    ],
                    pressMajors:[
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                        {major:'',minor:[]},
                        {major:'',minor:[]}
                    ]
                }
            },
            watch:{
                '$route'(){
                    if(this.minor == ''){
                        //
                        this.$axios.get('https://novel.juhe.im/category-info?gender=' + this.gender + '&type=' + this.type + '&major=' + this.major)
                        .then((response) => {
                        this.categorylist = response.data;
                        })
                    }else{
                        //
                        this.$axios.get('https://novel.juhe.im/category-info?gender=' + this.gender + '&type=' + this.type + '&major=' + this.major + '&minor=' + this.minor)
                        .then((response) => {
                        this.categorylist = response.data;
                        })
                    }
                    
                }
            },
            methods:{
                //gender
                changeGender1:function(b){                
                    this.major = b,
                    this.minor = ''
                    if(this.gender == 'female'){
                        this.defaultMoreSort = ['','','','','','']
                    }else if(this.gender == 'male'){
                        this.defaultMoreSort = ['','','','','']
                    }else{
                        this.defaultMoreSort = []
                    }
                },
                //url
                changeGender2:function(){
                    this.$router.push({
                        path:'/category-info',
                        query:{
                            gender:this.gender
                        }
                    })
                },
                //$route.query
                changeMajor1:function(b){
                    this.minor = ''
                    this.defaultMoreSort = b         
                },
                //url
                changeMajor2:function(){
                    this.$router.push({
                        path:'/category-info',
                        query:{
                            gender:this.gender,
                            major:this.major,
                            // minor:''
                        }
                    })
                },
                changeMoreSort1:function(a){
                    this.minor = a
                },
                changeMoreSort2:function(){
                    this.$router.push({
                        path:'/category-info',
                        query:{
                            gender:this.gender,
                            major:this.major,
                            minor:this.minor
                        }
                    })
                }
            },
            mounted() {
                this.$router.push({
                    path:'/category-info',
                    query:{
                        gender:this.gender
                    }
                })
                this.$axios.get('https://novel.juhe.im/category-info?gender=' + this.gender + '&type=' + this.type + '&major=' + this.major)
                .then((response) => {
                    this.categorylist = response.data;
                })
            }  
}
</script>

